export default class Subscription {
    constructor(api, channel) {
        this.api = api;
        this.channel = channel;
        this.errorCount = 0;
        console.log(this.channel);
        this.channel.setOnChannelError(this.onChannelError.bind(this));
        this.channel.setOnChannelOpen(this.onChannelOpen.bind(this));
    }
    delete() {
        this.channel.delete();
    }
    // Exists to allow subclasses to hook
    restart() {
        this.handleEvent({ data: { connection: 'reconnecting' } });
        this.start();
    }
    onChannelOpen(e) {
        this.errorCount = 0;
        this.handleEvent({ data: { connection: 'connected' } });
    }
    onChannelError(err) {
        console.error('event source error: ', err);
        this.errorCount++;
        if (this.errorCount >= 5) {
            console.error('bailing out, too many retries');
            this.handleEvent({ data: { connection: 'disconnected' } });
            return;
        }
        this.handleEvent({ data: { connection: 'reconnecting' } });
        setTimeout(() => {
            this.restart();
        }, Math.pow(2, this.errorCount - 1) * 750);
    }
    subscribe(path, app) {
        return this.api.subscribe(path, 'PUT', this.api.ship, app, this.handleEvent.bind(this), (err) => {
            console.log(err);
            this.subscribe(path, app);
        }, () => {
            this.subscribe(path, app);
        });
    }
    unsubscribe(id) {
        this.api.unsubscribe(id);
    }
    start() {
        this.subscribe('/primary', 'canvas-view');
    }
    handleEvent(data) {
        console.log('data', data);
        const json = data.data;
        if (json === null) {
            return;
        }
        console.log(data);
    }
}
