import _ from 'lodash';
export default class Api {
    constructor(ship, channel) {
        this.ship = ship;
        this.channel = channel;
        this.bindPaths = [];
    }
    unsubscribe(id) {
        this.channel.unsubscribe(id);
    }
    subscribe(path, method, ship = this.ship, app, success, fail, quit) {
        this.bindPaths = _.uniq([...this.bindPaths, path]);
        return this.channel.subscribe(this.ship, app, path, (err) => {
            fail(err);
        }, (event) => {
            success({
                data: event,
                from: {
                    ship,
                    path,
                },
            });
        }, (qui) => {
            quit(qui);
        });
    }
    action(appl, mark, data, ship = window.ship) {
        return new Promise((resolve, reject) => {
            this.channel.poke(ship, appl, mark, data, (json) => {
                resolve(json);
            }, (err) => {
                reject(err);
            });
        });
    }
    scry(app, path) {
        return fetch(`/~/scry/${app}${path}.json`).then((r) => r.json());
    }
    async spider(inputMark, outputMark, threadName, body) {
        const res = await fetch(`/spider/${inputMark}/${threadName}/${outputMark}.json`, {
            method: 'POST',
            body: JSON.stringify(body),
        });
        return res.json();
    }
}
